import {
    BaseElement,
    createClassName,
    defineElement,
    html,
    ifDefined,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { collapseStyles } from './collapseStyle';
import { CollapseProperties } from './collapseProperties';
import { CollapseEvent } from './collapseEvent';
import { EventDispatcherMixin, ResizableBehaviorMixin } from '@umetrics/sartorius-ui-mixins';

@defineElement({ tagName: 'collapse', styles: collapseStyles, version })
export class Collapse extends EventDispatcherMixin(ResizableBehaviorMixin(BaseElement)) implements CollapseProperties {
    // Static members
    public static readonly CURRENT_SLOT = 'current-slot';

    @property({ type: Boolean, reflect: true, attribute: 'no-animation' })
    public noAnimation = false;
    @property({ type: String, reflect: true, attribute: 'a11y-labeled-by' })
    public a11yLabeledBy = '';
    @property({ type: String, reflect: true, attribute: 'content-id' })
    public contentId = '';

    private _animating = false;
    private _desiredSize = '0px';
    private _open = false;
    private _horizontal = false;

    @state()
    private _classOpen = this._open;
    @state()
    private _classClosed = !this._open;

    @query('div')
    private _div;

    constructor() {
        super();
        this._addSingletonTransitionEventHandler = this._addSingletonTransitionEventHandler.bind(this);
        this._transitionEnd = this._transitionEnd.bind(this);
    }

    @property({ type: Boolean, reflect: true })
    get open() {
        return this._open;
    }
    set open(value) {
        const prev = this._open;
        if (prev !== value) {
            this._open = value;

            this.setAttribute('aria-hidden', '' + !this._open);

            // Focus the current collapse.
            if (this._open) {
                this.focus();
            }
            this._classClosed = false;
            this._updateSize(this._open ? 'auto' : '0px', true);
            this._classOpen = false;

            this.requestUpdate('open', prev);
        }
    }
    @property({ type: Boolean, reflect: true })
    get horizontal() {
        return this._horizontal;
    }
    set horizontal(value) {
        const prev = this._horizontal;
        if (prev !== value) {
            this._horizontal = value;
            if (this._div) {
                const otherDimension = this._dimensionMax === 'maxWidth' ? 'maxHeight' : 'maxWidth';
                this._div.style[otherDimension] = '';
            }
            this._updateSize(this._open ? 'auto' : '0px', false);
            this.requestUpdate('horizontal', prev);
        }
    }

    public toggle() {
        this.open = !this.open;
    }

    public show() {
        this.open = true;
    }

    public hide() {
        this.open = false;
    }

    protected firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);

        if (this._classClosed) {
            this._div.style[this._dimensionMax] = '0px';
            const otherDimension = this._dimensionMax === 'maxWidth' ? 'maxHeight' : 'maxWidth';
            this._div.style[otherDimension] = '';
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            closed: this._classClosed,
            open: this._classOpen,
        });

        return html`
            <div
                id="${ifDefined(this.contentId)}"
                class="${rootClasses}"
                role="region"
                aria-labelledby="${ifDefined(this.a11yLabeledBy)}"
            >
                <div class="${baseClassName}-inner">
                    <slot class="${baseClassName}-slot"></slot>
                </div>
            </div>
        `;
    }

    private afterReflow = (size, willAnimate) => {
        // Set the final size.
        this._div.style[this._dimensionMax] = size;
        if (!willAnimate) {
            this._transitionEnd();
        } else {
            this._addSingletonTransitionEventHandler(this, this._transitionEnd);
        }
    };

    private _updateSize(size, animated) {
        size = size === 'auto' ? '' : size; // Consider 'auto' as '', to take full size.

        let willAnimate = animated && !this.noAnimation && this._desiredSize !== size;
        this._desiredSize = size;
        this._updateTransition(false);

        requestAnimationFrame(async () => {
            if (this._div) {
                if (willAnimate) {
                    // Animation will start at the current size.
                    const startSize = this._calcSize();
                    // For `auto` we must calculate what is the final size for the animation.
                    // After the transition is done, _transitionEnd will set the size back to
                    // `auto`.
                    if (size === '') {
                        this._div.style[this._dimensionMax] = '';
                        size = this._calcSize();
                    }
                    this._div.style[this._dimensionMax] = startSize;

                    setTimeout(() => {
                        willAnimate = size !== startSize;
                        this._updateTransition(true);
                        this.afterReflow(size, willAnimate);
                    }, 0);
                } else {
                    this.afterReflow(size, willAnimate);
                }
            }
        });
    }

    private get _dimension() {
        return this.horizontal ? 'width' : 'height';
    }
    private get _dimensionMax() {
        return this.horizontal ? 'maxWidth' : 'maxHeight';
    }

    private _calcSize() {
        return this._div[this._dimension === 'width' ? 'offsetWidth' : 'offsetHeight'] + 'px';
    }

    private _updateTransition(enabled) {
        if (this._div) {
            this._div.style.transitionDuration = enabled && !this.noAnimation ? '' : '0s';
        }
    }

    private _transitionEnd() {
        this._div.style[this._dimensionMax] = this._desiredSize;
        this._classClosed = !this.open;
        this._classOpen = this.open;

        this._animating = false;
        this.notifyResize();
        this.dispatch(CollapseEvent.Toggle);
    }

    private _addSingletonTransitionEventHandler(elem: HTMLElement, callback) {
        this._animating = true;
        const timer = setTimeout(() => {
            if (this._animating) {
                callback();
                this._animating = false;
            }
        }, 500);

        elem.addEventListener(
            'transitionend',
            () => {
                clearTimeout(timer);
                callback();
                this._animating = false;
            },
            { once: true }
        );
    }
}
