import { cssSelector, GenerateStyles, style, css } from '@umetrics/sartorius-ui-base';

export const collapseStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: block;
                width: 100%;
            }

            ${root} {
                display: block;
                transition-duration: var(--expand-transition-duration, 250ms);
                -webkit-transition-duration: var(--expand-transition-duration, 250ms);
                overflow: visible;
            }

            ${child('inner')} {
                padding-bottom: var(--collapse-bottom-padding, 0px);
            }

            ${is('closed')} {
                display: none;
            }
            ${is('!open')} {
                overflow: hidden;
            }
        `,
    ];
};
