import { BaseElement } from '@umetrics/sartorius-ui-base';
import type { Constructor } from './constructor';

/**
 * Mixin EventDispatcherMixin for Sartorius-UI baseclass
 *
 * Adds boiler code for dispatching events
 *
 * Usage : class Demo extends EventDispatcherMixin(BaseElement) implements ... { ... }
 *
 */

export declare abstract class EventDispatcherMixinInterface extends BaseElement {
    public dispatch(
        event: string,
        detail?,
        target?,
        applyCallback?: null | (() => void),
        preventCallback?: null | (() => void)
    ): boolean;
}

export const EventDispatcherMixin = <T extends Constructor<BaseElement>>(superClass: T) => {
    abstract class MyMixinClass extends superClass {
        public dispatch(
            event,
            detail = {},
            target: HTMLElement = this as HTMLElement,
            applyCallback: null | (() => void) = null,
            preventCallback: null | (() => void) = null
        ): boolean {
            const applyDefault = target.dispatchEvent(
                new CustomEvent(event, {
                    bubbles: true,
                    composed: true,
                    cancelable: true,
                    detail,
                })
            );
            if (applyDefault && applyCallback) {
                // Apply, client commit behaviour
                applyCallback();
            } else if (preventCallback) {
                // Revert, client preventedDefault behaviour
                preventCallback();
            }
            return applyDefault;
        }
    }

    return MyMixinClass as unknown as Constructor<EventDispatcherMixinInterface> & T;
};
